ackground-color:rgba(157,157,157,.2)}.todoList{padding-top:15px;padding-bottom:15px}.todoItem{padding-top:5px;padding-bottom:5px;display:flex;align-items:center;justify-content:space-between;font-size:1.6rem}.todoItem .checkGroup{display:flex;align-items:center;gap:15px;width:80%}.todoItem .checkGroup input[type=checkbox]{width:20px;height:20px}.completedItem{text-decoration:line-through;color:#6c6c6c}.deleteBtn{position:relative;z-index:0;width:20px;height:2px;padding:5px;cursor:pointer;transition:transform .2s ease-in}.deleteBtn:hover{transform:scale(1.2)}.deleteBtn::after,.deleteBtn::before{content:"";position:absolute;left:0;z-index:1;width:20px;height:2px;background-color:#9d9d9d}.deleteBtn::after{transform:rotate(-45deg)}.deleteBtn::before{transform:rotate(45deg)}